.vidoRow {
    display: flex;
    margin-bottom: 30px;
    max-width: 700px;
}

.vidoRow>img {
    object-fit: contain;
    width: 246px;
    height: 138px;
}

.videorow_text {
    margin-left: 14px;
}

.videorow_headline {
    font-size: 12px;
    color: #606060;
}

.videorow_description {
    margin-top: 20px;
    font-size: 12px;
    color: #606060;
}

.videorow_subscribers {
    font-weight: bolder;
    color: rgba(84, 157, 211)
}

.videorow_subs {
    background-color: lightgray;
    padding: 2px;
}